import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { useForm } from "react-hook-form";
import { createPasskey, registerWithPasskey } from "./webauthn";
import Collapse from "@mui/material/Collapse";
import Alert from "@mui/material/Alert";
import { useState } from "react";
import { zodResolver } from "@hookform/resolvers/zod";
import { registerSchema, type RegisterInfo } from "./schemaUtils";

function Register() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: zodResolver(registerSchema),
  });
  const [err, setErr] = useState<string | null>(null);
  const [info, setInfo] = useState<string | null>(null);

  async function onSubmit(data: RegisterInfo) {
    setErr(null);
    setInfo(null);
    try {
      setInfo(" Passkey...");
      const cred = await createPasskey(data.userName, data.displayName);
      setInfo("Passkey , ...");
      const result = await registerWithPasskey(cred);
      setInfo(": " + result.userName);
    } catch (error) {
      setErr(error instanceof Error ? error.message : "Unknown error");
    }
  }

  return (
    <Stack
      component="form"
      onSubmit={handleSubmit(onSubmit)}
      alignItems="center"
      justifyContent="center"
      sx={{ height: "100vh" }}
    >
      <h1></h1>
      <TextField
        label=""
        variant="outlined"
        margin="normal"
        fullWidth
        placeholder=""
        {...register("userName")}
        error={!!errors.userName}
        helperText={errors.userName ? errors.userName.message : ""}
      />
      <TextField
        label=""
        variant="outlined"
        margin="normal"
        placeholder=""
        fullWidth
        {...register("displayName")}
        error={!!errors.displayName}
        helperText={errors.displayName ? errors.displayName.message : ""}
      />
      <Button variant="contained" color="primary" type="submit">
        
      </Button>
      <Collapse in={!!err} sx={{ width: "100%", marginTop: 2 }}>
        <Alert
          severity="error"
          onClose={() => setErr(null)}
          sx={{ wordBreak: "break-all" }}
        >
          {err}
        </Alert>
      </Collapse>
      <Collapse in={!!info} sx={{ width: "100%", marginTop: 2 }}>
        <Alert severity="info" onClose={() => setInfo(null)}>
          {info}
        </Alert>
      </Collapse>
    </Stack>
  );
}

export default Register;
